<template>

  <div class="mw-page page purchase" v-nav="{hideNavbar:true}">
    <div class="mw-page-header">
      <i class="left icon ion-ios-arrow-back" @click="$router.go(-1)"></i>
      <span></span>
      <i class="right"></i>
    </div>
    <div class="mw-page-content">
      <!--<cst-item label="" v-model="inquiry_info.sup_name" :hasArrow="true" :action="action" :canEdit="false" @click.native="chooseDic('customer')"></cst-item>-->
      <!--<div class="item-divider" style="padding-left: 15px"></div>-->

      <div class="item-divider" v-show="action==='new'" style="padding-left: 15px"></div>
      <cst-item label="" v-show="action==='new'" v-model="inquiry_info.purch_man_name" :hasArrow="false" :action="action" :canEdit="false"></cst-item>
      <cst-item label="" v-show="action==='new'" v-model="inquiry_info.purch_man_phone" :hasArrow="false" :action="action" :canEdit="false"></cst-item>
      <cst-item label="Email" v-show="action==='new'" v-model="inquiry_info.purch_man_email" :hasArrow="false" :action="action" :canEdit="false"></cst-item>
      <div class="item-divider" style="padding-left: 15px"></div>
      <cst-item v-if="action==='check'" label="" v-model="inquiry_info.quote_end_date" :hasArrow="false" :action="action" :canEdit="false"></cst-item>
      <datepicker  v-else v-model="inquiry_info.quote_end_date" label="" date-format="yyyy-mm-dd"></datepicker>

      <cst-item v-if="action==='check'" label="" v-model="inquiry_info.delivery_date" :hasArrow="false" :action="action" :canEdit="false"></cst-item>
      <datepicker  v-else  v-model="inquiry_info.delivery_date" label="" date-format="yyyy-mm-dd"></datepicker>
      <cst-item label="" v-model="inquiry_info.delivery_place" :action="'check'"></cst-item>
      <cst-item label="" v-model="inquiry_info.coin_name" :hasArrow="true" :action="action" :canEdit="false" @click.native="chooseDic('coin')"></cst-item>
      <cst-item label="" v-model="inquiry_info.invoice_type_name" :hasArrow="true" :action="action" :canEdit="false" @click.native="chooseDic('invoiceType')"></cst-item>
      <cst-item label="" v-model="inquiry_info.tax_ratio" :action="action" :canEdit="true" editType="number"></cst-item>
      <!--<cst-item label="" v-model="inquiry_info.rebate" :action="action" :canEdit="true" editType="number"></cst-item>-->
      <div v-for="item in inquiry_info.mat_list" style="margin-top: 8px">
        <swipe-item swipeItemText="" :obj="item"  v-on:ItemClick="onItemClick">
          
          <span class="item-note normal">{{item.mat_desc}} <i :class="['left icon',item.col? 'ion-ios-arrow-down':'ion-ios-arrow-up']"></i></span>

        </swipe-item>
        <div style="overflow: hidden" :class="['col-content',item.col?'coll':'']">
          <item>
            
            <span v-if="action==='check'" class="item-note">{{item.up}}</span>
            <input v-else class="modelInput" type="number" v-model="item.up" >
          </item>
          <item>
            
            <span  class="item-note">{{item.quantity}}</span>
          </item>
          <item>
            /
            <span v-if="action==='check'" class="item-note">{{item.process_up}}</span>
            <input v-else class="modelInput" type="number" v-model="item.process_up" >
          </item>
          <item>
            
            <span class="item-note">{{item.process_cost}}</span>
          </item>
          <item>
            
            <span  class="item-note">{{item.total_price}}</span>
          </item>
        </div>
      </div>
      <item style="margin-top: 8px">
        {{ totalPrice }}
        <span class="item-note">{{ inquiry_info.total_price }}</span>
      </item>
      <!--<textarea placeholder="" :disabled="this.action!='action'" v-if="inquiry_info.remark==''||inquiry_info.remark==null"  v-text="inquiry_info.remark" style="background: #fff"></textarea>-->
      <textarea  disabled v-show="this.action!=='action'"  >{{ inquiry_info.remark }}</textarea>
      <textarea  placeholder=""   v-show="this.action==='action'"  v-model="inquiry_info.remark" ></textarea>
    </div>
    <sidebar-check :showFilter="showFilter" :dicType="dicType" :chosen="chosen" v-on:hideMask="showFilter=false"></sidebar-check>
    <div class="mw-page-footer" v-if="action=='action'">
      <div class="btns">
        <!--<div class="btn">-->
        <!---->
        <!--</div>-->
        <!--<div class="divider"></div>-->
        <div class="btn" @click="submitFeedback()">
          
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import CstItem from "../../../components/cstItem";
  import SwipeItem from "../../../components/swipeItem";
  import  sess from '@/utils/sess'
  import {todayDate} from '@/utils'
  import {mapGetters} from 'vuex'
  import SidebarCheck from "../../../components/sidebarCheck";
  import { supInquiryFeedbackDetail,submitFeddback } from '@/api/purchase/inquiry';
  export default{
    name:'inquiryFeedback',
    components: {
      SidebarCheck,
      SwipeItem,
      CstItem},
    data(){
      return {
        action:'check',
        dicType:'coin',
        showFilter:false,
        feedback_id:1,
        inquiry_info: {
          //
          "coin": "1",
          "coin_name": "",
          "deal_flag": 0,
          "delivery_date": todayDate(),
          "delivery_place": "",
          "inquiry_date":'',
          "inquiry_id": '',
          "invoice_type": 1,
          "invoice_type_name": '',
          "rebate":1,
          "mat_list": [
            {
              "inquiry_detail_id": 1,
              "mat_desc": "1",
              "mat_id": 28876,
              "mat_model": "38i3",
              "mat_no": "4a35",
              "mat_type_id": 41348,
              "mat_type_name": "xo8i",
              "process_cost": 67803,
              "process_up": 3,
              "quantity": 2,
              "tax_cost": 74350,
              "total_price": 1,
              "up": 1
            },
            {
              "inquiry_detail_id": 1,
              "mat_desc": "2",
              "mat_id": 28876,
              "mat_model": "38i3",
              "mat_no": "4a35",
              "mat_type_id": 41348,
              "mat_type_name": "xo8i",
              "process_cost": 67803,
              "process_up": 1,
              "quantity": 2,
              "tax_cost": 74350,
              "total_price": 83318,
              "up": 3
            }
          ],
          "purch_man": 47441,
          "purch_man_email": "sz18",
          "purch_man_name": "6jl4",
          "purch_man_phone": "t27x",
          "quote_date": "zac0",
          "quote_end_date": todayDate(),
          "remark": "k73n",
          "sup_list": [
            {
              "sup_company_id": 44717,
              "sup_name": "8f78"
            }
          ],
          "tax_ratio": 11127,
          "total_price": 46101
        }

      }

    },
    mounted:function()  {
        this.supplierFeedbackDetail()
    },
    methods: {
      onMenuClick(){

      },
      onItemClick(item){
        this.$set(item,'col',!item.col)
      },
      chosen(item){
        switch (this.dicType){
          case 'coin':{
            this.inquiry_info.coin = item.code
            this.inquiry_info.coin_name = item.name
            break;
          }
          case 'invoiceType':{
            this.inquiry_info.invoice_type = item.code
            this.inquiry_info.invoice_type_name = item.name
            break;
          }
        }
      },
      chooseDic(dicType){
        if(this.action=="action") {
          switch (dicType) {
            case 'coin': {
              this.dicType = 'coin'
              this.showFilter = true
              break;
            }
            case 'invoiceType': {
              this.dicType = 'invoiceType'
              this.showFilter = true
              break;
            }
          }
        }
      },
      //
      supplierFeedbackDetail(){
        const requestData={
          query:{inquiry_id:this.$route.params.inquiry_id},
//          inquiry_id:this.$route.params.inquiry_id,
          inquiry_id:8
        };
        supInquiryFeedbackDetail(requestData).then(res=>{
          this.inquiry_info= res.inquiry_feedback;
        }).catch(error=>{
          $toast.show(error.description);
        })
      },
      //
      submitFeedback(){
        const requestData={
          inquiry_feedback:this.inquiry_info
        };
        submitFeddback(requestData).then(res=>{
          $toast.show("");
          this.action="check"
          $router.go(-1)
        }).catch(error=>{
          $toast.show(error.description);
        })
      }

    },
    computed:{
      ...mapGetters(['userinfo']),
      totalPrice:function(){
        if(this.$route.params.action==='action'){
          let totalPrice=0
          this.inquiry_info.mat_list.forEach((item,i)=>{
            item.process_up=Number(item.process_up).toFixed(2)//
            item.up=Number(item.up).toFixed(2)//
            item.quantity=Number(item.quantity)//
            if(this.inquiry_info.rebate){
              this.inquiry_info.rebate=Number(this.inquiry_info.rebate)//
            }else{
              this.inquiry_info.rebate=1
            }
            item.process_cost=(item.quantity*item.process_up*this.inquiry_info.rebate).toFixed(2);
            item.total_price=(item.quantity*(Number(item.process_up)+Number(item.up))*this.inquiry_info.rebate).toFixed(2);
            totalPrice+=Number(item.total_price);
            this.inquiry_info.total_price=totalPrice.toFixed(2);
          })
        }


      }

    },
    created(){
      this.action = this.$route.params.action
      if(this.$route.params.inquiry_id==='null'&&this.action==='new'){

        this.inquiry_info.purch_man_name = this.userinfo.username
        this.inquiry_info.purch_man_email = this.userinfo.email
        this.inquiry_info.purch_man_phone = this.userinfo.phone
        this.inquiry_info.inquiry_date = todayDate()
        const mats = sess.get('mats')
        this.inquiry_info.mat_list = JSON.parse(mats)
        sess.remove('mats')
      }
    },
  }
</script>

<style lang="scss" scoped>

  .col-content{
    height: 250px;
  }
  .modelInput{float: right;height: 14px;line-height: 14px;text-align: right;color: #ababab}
  .item-note{color: #666}
</style>



